.souvenirs {
	&__content {
		float: none;
		padding-top: 45px;
		margin: 0 auto;
		width: 100%;
		max-width: 700px; }
	&__items {
		float: none;
		padding: 0;
		margin: 0 auto;
		width: 100%;
		max-width: 500px; }
	&__items-in {
		margin: -40px 0 0; }
	&__description {
		@include f(null, 20, 36); }
	&__form {
		float: left;
		width: 560px;
		margin-bottom: 15px; }
	&__col {
		width: 50%;
		&:first-child {
			margin-right: 12px; } }
	&__prev,
	&__next {
		top: 230px;
		.icon {
			font-size: 82px; } }
	&__down {
		display: none; } }

.souvenir {
	&__img {
		height: 400px; }
	&__title,
	&__description {
		color: #fff; } }
.souvenirs-bg {
	top: -205px;
	left: -200px; }
